span {
  font-family: monospace;
}

h1, h3 {
  margin: 5px;
}
p {
  color: #3B3142;
}
body {
  /* background-image: linear-gradient(#9E856C, #FEDD59); */
  /* background-image: linear-gradient(white, #FEDD59); */
  background-image: radial-gradient(#D90368, #3B3142);
  background-size: cover;
  background-repeat: no-repeat;
  color: #3B3142;
}

.App {
  
  text-align: center;
  max-width: 2000px;
  width: 80%;
  min-width:400px;
  margin-left: auto;
  margin-right: auto;
  height: 100vh;
  display: grid;
  grid-template: repeat(40,1fr)/repeat(40,1fr);
}



.App-header {
  /* background-image:url(/images/header-animation.gif); */
  background-color: transparent;
  color: white;
  background-size:cover;  
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  grid-row: 2/span 5;
  grid-column:1/ 41;
}

.App-link {
  color: #61dafb;
}

.white {
  background-color: #F6F7EB;
  
}
.red {
  background-color: #D90368;
}
.green {
  background-color: #C3E991;
}
.grey {
  background-color: #999497;
}
.greyText {
  color: grey;
}
.blackText {
  color: black;
}

.typingAreaContainer {
  position: relative;
  min-width: 100%;
  min-height: 100%;
}
.pauseOverlay {
  position: absolute; /* Sit on top of the page content */
  /* display: none; Hidden by default */
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  place-self: center;
  background-image: radial-gradient(rgba(255,255,255,1 ), rgba(255,255,255,0.5)); /* Black background with opacity */
  cursor: pointer; /* Add a pointer on hover */
  z-index: 1000;
}
.pauseOverlayText {
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: large;
  color: #F77F00;
  transform: translate(-50%,-50%);
  place-self: center;
  -ms-transform: translate(-50%,-50%);
}

.finishedOverlay {
  position: absolute; /* Sit on top of the page content */
  /* display: none; Hidden by default */
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  place-self: center;
  background-image: radial-gradient(rgba(255,255,255,0.8 ), rgba(255,255,255,0.2)); /* Black background with opacity */
  cursor: pointer; /* Add a pointer on hover */
  z-index: 1000;
}
.finishedOverlayText {
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: large;
  color: #F77F00;
  transform: translate(-50%,-50%);
  place-self: center;
  -ms-transform: translate(-50%,-50%);
}

.slider {
  margin-left: auto;
  margin-right: auto;
}

.typingArea {
  position: relative;
  padding: 2vh;
  background-color: #F6F7EB;
  font-size: 160%;
  grid-row:8 / span 21;
  grid-column: 1/ 30;
  white-space: pre-wrap;
  overflow: auto;
}

.settingsArea {
  background-color: #F6F7EB;
  display: flexbox;
  grid-row: 30/40;
  grid-column: 1/ 41;
}

.statsArea {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  background-color: #F6F7EB;
  padding: 1vw;
  grid-row: 8/span 21;
  grid-column: 31/ 42;
}
.statsArea >p,button{
  margin: 1px;
  padding: 2px;
  font-size: 140%;
  flex-grow: 1;
  border: 3px solid rgb(247, 127, 0,0.2);
  border-radius: 5px;
}


.settingsTitle{
  padding: 0.8em;
  margin: 0.2em;
  font-size: 120%;
  font-weight: bold;
  color: #D90368;
 
}

.newTTTButton {
	box-shadow: 0 1vh 1vh -1vh #3b3142;
	background-color: #F77F00;
  border-radius:8px;
  outline-style: none;
  border-style: none;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	padding: 1vh;
	text-decoration:none;
}
.newTTTButton:hover {
	background:linear-gradient(to bottom, #3b3142 5%, #d90367 100%);
	background-color:#3b3142;
}
.newTTTButton:active {
	position:relative;
	top:1px;
}

.actionButton {
	box-shadow: 0 1vh 1vh -1vh #3b3142;
	background-color: #F77F00;
  border-radius:8px;
  outline-style: none;
  border-style: none;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	padding: 1vh;
	text-decoration:none;
}
.actionButton:hover {
	background:linear-gradient(to bottom, #3b3142 5%, #d90367 100%);
	background-color:#3b3142;
}
.actionButton:active {
	position:relative;
	top:1px;
}


.tab-list {
  border-bottom: 3px solid rgb(247, 127, 0,0.2);
  padding-left: 0;
  cursor: pointer;
  margin: 0;
}

.tab-list-item {
  display: inline-block;
  list-style: none;
  margin-bottom: -1px;
  padding: 0.5rem 0.75rem;
  border-radius: 5px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  border: none;
  border-style: none;
  outline-style: none;
}

.tab-list-active {
  background-color: #F77F00;
  color: white;
}

@media only screen and (max-width: 400px) {
  .App {
    min-width: 100vw;
    max-height:300vh;
  }
  .App-header{
    grid-row:1/5;
  }
  .statsArea{
    grid-row: 15/21;
    grid-column:1/41;
    max-width:100vw;
  }
  .typingArea {
    grid-row: 6/14;
    grid-column:1/41;

  }
  .settingsArea{
    grid-row: 22/30;
    
  }
}